import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import BtnAddNew from 'components/common/button/BtnAddNew';
import FormSearchStudent from './FormSearchStudent';
import { tochucthi } from 'dump/data';

const urlForm = '#exam-organization/form';
const columns = [
  {
    title: 'M SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tn lp',
    dataIndex: 'className',
    key: 'toScores',
  },
  {
    title: 'Phng thi',
    dataIndex: 'phongthi',
    key: 'toScores',
  },
  {
    title: 'Ghi ch',
    dataIndex: 'note',
    key: 'toScores',
  },
  {
    title: 'Thao tc',
    dataIndex: 'operation',
    render: (text, record) => (
      <Fragment>
        <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
          <BtnDelete />
        </Popconfirm>
      </Fragment>
    ),
  },
];
class ExamOrganization extends Component {
  render() {
    return (
      <Card title="T chc thi">
        <Row>
          <Col span={24}>
            <FormSearchStudent />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={tochucthi} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExamOrganization />
  </Page>
);



