import React, { Component } from 'react';
import { Row, Col, Form, Input, Checkbox, Select, Radio } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;
const RadioGroup = Radio.Group;
@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Hc k">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Hc k 1</Option>
                  <Option value="2">Hc k 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="4">2020-2021</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ln thi">
              {getFieldDecorator('class', { initialValue: '' })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="t thi">
              {getFieldDecorator('dt', { initialValue: '' })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('hp', {})(
                <Select>
                  <Option value="1">Cng tc vn th v lu tr</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngy gi thi" hasFeedback>
              {getFieldDecorator('birthdate')(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label=" ">
              {getFieldDecorator('dn', {})(
                <RadioGroup>
                  <Radio value={1}>HP Nin ch</Radio>
                  <Radio value={2}>HP tn ch</Radio>
                </RadioGroup>
              )}
            </Form.Item>
          </Col>
          <Col span={24}>
            <Form.Item label="T chc nhiu mn hc">
              {getFieldDecorator('tchucnhiumonhoc', {})(<Checkbox />)}
            </Form.Item>
          </Col>
        </Row>
        <Row />
      </Form>
    );
  }
}

export default FormSearchStudent;



